.header_nav {
    margin: auto 0;
    /* list-style-type: none; */
}

.header_nav li {
    margin: 0 0px 0 10px;
    list-style-type: none;
    text-align: end;
}

.header_nav li a {
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    color: #ffb400;
    line-height: 50px;
}

@media screen and (max-width: 523px) {
    .header_nav li a {
        line-height: 30px;
    }
}
